@font-face {
  font-family: "Compose";
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/Compose-Light.ttf") format("ttf"), url("../fonts/Compose-Light.woff") format("woff"), url("../../fonts/Compose-Light.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Compose-Regular.ttf") format("ttf"), url("../fonts/Compose-Regular.woff") format("woff"), url("../../fonts/Compose-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Compose-Medium.ttf") format("ttf"), url("../fonts/Compose-Medium.woff") format("woff"), url("../../fonts/Compose-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Compose-SemiBold.ttf") format("ttf"), url("../fonts/Compose-SemiBold.woff") format("woff"), url("../../fonts/Compose-SemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Compose-Bold.ttf") format("ttf"), url("../fonts/Compose-Bold.woff") format("woff"), url("../../fonts/Compose-Bold.woff2") format("woff2");
}
.svg-image-arrow-white-up-45 {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-arrow-white-up-45 {
  background-position: 0 0;
  background-size: 100% auto;
  width: 30px;
}

.svg-image-arrow-white-up-45:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-image-arrow-white-up-45-dims {
  width: 30px;
  height: 30px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

:root {
  --white-color: #fff;
  --blue-color: #00114f;
  --main-font: "Compose", sans-serif;
  --roboto: "Roboto", sans-serif;
  --transition: all 0.2s ease-out;
}

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--main-font);
  color: var(--blue-color);
  background-color: #fff;
  min-width: 320px;
}
body.lock {
  overflow: hidden;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 1500px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 63px;
}
@media only screen and (max-width: 1399px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 768px) {
  .container {
    padding: 0 28px;
  }
}

.header.white,
.header.white .logo__main,
.header.white .menu__link,
.header.white .auth__login,
.header.white .auth__signup,
.header.white .lang__selected .lang__value {
  color: var(--white-color);
}
.header.white .auth__signup {
  border: 1px solid rgba(255, 255, 255, 0.6);
}
.header.white .auth__signup:hover {
  border: 1px solid var(--blue-color);
}
.header.white .menu__item::after {
  border: 1px solid var(--white-color);
}
.header.white .header__line {
  background-color: var(--white-color);
}

.header.dark,
.header.dark .logo__main,
.header.dark .menu__link,
.header.dark .auth__login,
.header.dark .auth__signup,
.header.dark .lang__selected .lang__value {
  color: var(--blue-color);
}
.header.dark .auth__signup {
  border: 1px solid rgba(0, 17, 79, 0.6);
}
.header.dark .auth__signup:hover {
  border: 1px solid var(--blue-color);
}
.header.dark .menu__item::after {
  border: 1px solid var(--blue-color);
}
.header.dark .header__line {
  background-color: var(--blue-color);
}

.header {
  position: absolute;
  top: 41px;
  left: 0;
  width: 100%;
}

.header__container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.header__navbar {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 38px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__main {
  position: relative;
  top: -5px;
  margin-right: 18px;
  font-weight: 700;
  font-size: 35px;
  line-height: 44px;
  text-decoration: none;
}

.logo__descr {
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  letter-spacing: -0.01em;
}

.header__line {
  margin-right: 51px;
  width: 1px;
  height: 59px;
}
@media only screen and (max-width: 1399px) {
  .header__line {
    margin-right: 35px;
  }
}

.header__menu {
  margin-right: 117px;
}
@media only screen and (max-width: 1399px) {
  .header__menu {
    margin-right: 60px;
  }
}

.menu__list {
  min-width: 394px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 1399px) {
  .menu__list {
    min-width: 380px;
  }
}

.menu__item {
  position: relative;
  list-style-type: none;
}
.menu__item::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  transition: var(--transition);
}
.menu__item:hover::after {
  visibility: visible;
  opacity: 1;
  transform: translateY(4px);
}

.menu__link {
  font-weight: 400;
  font-size: 21px;
  line-height: 26px;
  letter-spacing: -0.01em;
  text-decoration: none;
}

.lang {
  position: relative;
}
.lang .lang__shevron {
  transition: var(--transition);
}
.lang.active .lang__shevron {
  transform: scaleY(-1);
}
.lang.active .lang__selected::after {
  display: block;
}

.lang__selected {
  display: flex;
  align-items: center;
  gap: 0 12px;
  cursor: pointer;
}
.lang__selected .lang__icon img {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.lang__selected::after {
  display: none;
  content: "";
  position: absolute;
  top: 28px;
  left: -30%;
  right: -50%;
  height: 70px;
}

.lang__value {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
}

.lang__icon {
  width: 29px;
  height: 29px;
}
.lang__icon img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 50%;
}

.lang.active .lang__dropdown {
  visibility: visible;
  opacity: 1;
}

.lang__dropdown {
  position: absolute;
  top: 67px;
  left: -32px;
  z-index: 2px;
  width: 205px;
  min-height: 228px;
  padding: 13px 13px 20px;
  background: var(--white-color);
  box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  visibility: hidden;
  opacity: 0;
  transition: var(--transition);
}
.lang__dropdown .lang__value {
  color: var(--blue-color);
}

.lang__item {
  display: flex;
  align-items: center;
  gap: 0 13px;
  padding: 7px 14px 5px;
  cursor: pointer;
  transition: var(--transition);
}
.lang__item .lang__icon {
  width: 35px;
  height: 35px;
}
.lang__item .lang__value {
  font-weight: 600;
  font-size: 17px;
  line-height: 37px;
  color: var(--blue-color);
}
.lang__item:hover {
  background-color: rgba(0, 17, 79, 0.1);
  border-radius: 6px;
}

.auth {
  display: flex;
  align-items: flex-end;
}

.auth__login,
.auth__signup {
  display: flex;
  align-items: flex-end;
  height: 68px;
  margin-top: 9px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
  text-decoration: none;
  transition: var(--transition);
}

.auth__login {
  margin-right: 20px;
  width: 145px;
  padding: 24px 0 18px 0;
  justify-content: center;
}
.auth__login:hover {
  background-color: var(--blue-color);
  border-radius: 6px;
}

.auth__signup {
  width: 188px;
  padding: 24px 30px 18px 41px;
}
.auth__signup .icon__arrow {
  display: inline-block;
  margin-left: 27px;
  transform: rotate(-45deg);
  transition: var(--transition);
}
.auth__signup:hover {
  background-color: var(--blue-color);
  border: 1px solid var(--blue-color);
  border-radius: 6px;
}
.auth__signup:hover .icon__arrow {
  transform: rotate(0);
}

.dark .auth__login:hover,
.dark .auth__signup:hover {
  color: var(--white-color);
}

.dark .auth__signup:hover path {
  fill: var(--white-color);
  stroke: var(--white-color);
}

.main {
  flex-grow: 1;
}

.intro {
  width: 100%;
  height: 100vh;
  min-width: 500px;
}

.intro__slider,
.intro__wrapper {
  width: 100%;
  height: 100%;
}

.intro__item {
  width: 100%;
  height: 100%;
}

.intro__bg {
  width: 100%;
  height: 100%;
}
.intro__bg img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

footer {
  padding: 20px;
}